import * as tf from '@tensorflow/tfjs'
import '@tensorflow/tfjs-backend-webgl';

import { RendererCanvas, Vector3, remap, PolynomialRegression } from '../../../../lib';

export class Solution extends RendererCanvas {
    t = 0.0;
    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: Vector3[] = [];
    selVec: Vector3 | undefined = undefined;

    col: string[] = ['#ff0000', '#00ff00', '#0000ff', '#00fff0', '#ff0000', '#fff000', '#0fff00'];

    tfLinearRegression: PolynomialRegression[] = [];

    container: HTMLDivElement;

    constructor(id: string) {
        const divHost = document.getElementById(id);
    
        const container = document.createElement('div');
        super(container);

        divHost.appendChild(container);
        container.id = 'container-lab'
        container.style.alignItems = 'center';
        container.style.paddingTop = '50px';
        container.style.marginLeft = 'auto';
        container.style.marginRight = 'auto';
        container.style.width = '600px';
        container.style.height = '500px';
        this.container = container;
        this.canvas.width = 600;
        this.canvas.height = 500;


        // TODO
        // You code goes here for one time operation

        this.tfLinearRegression.push(new PolynomialRegression(1));
        this.tfLinearRegression.push(new PolynomialRegression(2));
        this.tfLinearRegression.push(new PolynomialRegression(3));
        this.tfLinearRegression.push(new PolynomialRegression(4));

        getData().then((d: any) => {
            let xMin = Number.MAX_SAFE_INTEGER;
            let xMax = Number.MIN_SAFE_INTEGER;
            let yMin = Number.MAX_SAFE_INTEGER;
            let yMax = Number.MIN_SAFE_INTEGER;

            d.map((data: any) => {
                let x = (data['horsepower']);
                if (x > xMax) { xMax = x; }
                if (x < xMin) { xMin = x; }

                let y = (data['mpg']);
                if (y > yMax) { yMax = y; }
                if (y < yMin) { yMin = y; }
                return undefined
            });

            for (let data of d) {
                let x = (data['horsepower']);
                let y = (data['mpg']);
                this.vs.push(new Vector3(
                    remap(x, xMin, xMax, -0.9, 0.9),
                    remap(y, yMin, yMax, -0.9, 0.9)));
            }
        });
        this.initUI();
        // !! you should execute it for rendering
        this.start();
    }
    public destroy(): void {
    //     console.log('???');
        this.isStatic = true;
        try {
            while (this.container.parentElement) {
                this.container.parentElement.removeChild(this.container);
            }
            try {
                while (this.divHost && this.divHost.lastElementChild) {
                    const lastChild = this.divHost.lastElementChild as Node;
                    this.divHost.removeChild(lastChild);
                }
            } catch (error) {
                console.error('Error removing child elements:', error);
            }
            
        } catch (error) { }
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop

        this.RenderIndependentCanvas(ctx);

        // !! can stop render after this frame.
        // this.isStatic = true;
    }
    public mouseDown(x: number, y: number): void {
        const m = new Vector3(this.mapX(x), this.mapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            if (Vector3.distanceTo(m, this.vs[i]) < 0.05) {
                this.selVec = this.vs[i];
                return;
            }
        }
        this.vs.push(new Vector3(this.mapX(x), this.mapY(y)));
    }
    public mouseUp(x: number, y: number): void {
        // TODO
    }
    public mouseDrag(x: number, y: number): void {
        if (this.selVec) {
            this.selVec.x = this.mapX(x);
            this.selVec.y = this.mapY(y);
        }
    }
    public mouseClick(x: number, y: number): void {
        const m = new Vector3(this.mapX(x), this.mapY(y));
        this.vs.push(new Vector3(m.x, m.y))
    }
    public mouseMove(x: number, y: number) {
        let selVec: undefined | Vector3 = undefined;
        const m = new Vector3(this.mapX(x), this.mapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            let theDis = Vector3.distanceTo(m, this.vs[i]);
            if (theDis < 0.05) {
                selVec = this.vs[i];
            }
        }
        if (selVec) {
            this.selVec = selVec;
        } else {
            this.selVec = undefined;
        }
    }
    public keyDown(k: KeyboardEvent): void {
        // TODO
    }
    mapX(v: number) {
        return remap(v, 0, this.canvas.width, -1, 1);
    }
    mapY(v: number) {
        return remap(v, 0, this.canvas.height, 1, -1);
    }
    GridLine(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = '#555555';
        const xInterval = this.canvas.width / 20 - 1;
        const yInterval = this.canvas.height / 20 - 1;
        for (let i = 0; i < 20; i += 1) {
            if (i === 10) {
                ctx.lineWidth = 3;
            } else {
                ctx.lineWidth = 1;
            }
            let x0 = xInterval * i;
            let y0 = 0;
            let x1 = x0;
            let y1 = this.canvas.height;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();

            x0 = 0;
            y0 = yInterval * i;
            x1 = this.canvas.width;
            y1 = y0;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();
        }
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.t += 5
    //     console.log(this.t)
        const len = Math.floor(this.t) < this.vs.length ? Math.floor(this.t) : this.vs.length;

        // ctx.translate(ctx.canvas.width * 0.5, ctx.canvas.height * 0.5);

        ctx.fillStyle = '#222';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.GridLine(ctx);

        if (this.vs.length > 1) {

            for (let m = 0; m < this.tfLinearRegression.length; ++m) {
                const xx = [], yy = [];
                for (let i = 0; i < this.vs.length; ++i) { xx.push(this.vs[i].x); yy.push(this.vs[i].y); }
                // ............................ train
                this.tfLinearRegression[m].train(xx, yy);
                // ............................ end train

                this.RenderLine(ctx);
            }
        }


        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < len; ++i) {
            ctx.beginPath();
            ctx.arc(
                remap(this.vs[i].x, -1, 1, 0, this.canvas.width),
                remap(this.vs[i].y, -1, 1, this.canvas.height, 0),
                5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }

        if (this.selVec) {
            ctx.fillStyle = '#ffffff';
            ctx.font = '15px Arial';
            ctx.textAlign = 'center';
            ctx.fillText(`x:${this.selVec.x.toFixed(3)}, y${this.selVec.y.toFixed(3)}`,
                remap(this.selVec.x, -1, 1, 0, this.canvas.width),
                remap(this.selVec.y, -1, 1, this.canvas.height, 0) - 9,
            );
        }

    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        for (let m = 0; m < this.tfLinearRegression.length; ++m) {
            const curveX: number[] = [];
            for (let x = -1; x <= 1; x += 0.08) {
                curveX.push(x);
            }

            // ............................ predict
            const ys = tf.tidy(() => {
                return this.tfLinearRegression[m].predict(tf.tensor1d(curveX));
            });

            let curveY = ys.dataSync();
            ys.dispose();
            // ............................ end predict

            ctx.lineWidth = 1;
            ctx.strokeStyle = this.col[m]; // '#00fff0';
            ctx.beginPath();
            ctx.moveTo(remap(curveX[0], -1, 1, 0, this.canvas.width), remap(curveY[0], -1, 1, this.canvas.height, 0));

            for (let i = 1; i < curveX.length; ++i) {
                ctx.lineTo(remap(curveX[i], -1, 1, 0, this.canvas.width), remap(curveY[i], -1, 1, this.canvas.height, 0));
            }
            ctx.stroke();
        }

    }
    public initUI() {
        const div = document.createElement('div');
        // div.style.background = '#dddddd';
        div.style.position = 'absolute';
        // div.style.marginTop = '600px';
        // div.style.left = '40px';
        div.style.color = '#ffffff';
        // div.style.background = '#000';

        let t = document.createElement('p');
        t.textContent = 'click on this screen to add data';
        div.append(t);

        t = document.createElement('p');
        t.textContent = 'LinearRegression';
        t.style.marginTop = '0px';
        t.style.marginBottom = '0px';
        t.style.color = this.col[0];
        div.append(t);

        t = document.createElement('p');
        t.style.marginTop = '0px';
        t.style.marginBottom = '0px';
        t.style.color = this.col[1];
        t.textContent = 'Degree of polynomial - 2';
        div.append(t);

        t = document.createElement('p');
        t.style.marginTop = '0px';
        t.style.marginBottom = '0px';
        t.style.color = this.col[2];
        t.textContent = 'Degree of polynomial - 3';
        div.append(t);

        t = document.createElement('p');
        t.style.marginTop = '0px';
        t.style.marginBottom = '0px';
        t.style.color = this.col[3];
        t.textContent = 'Degree of polynomial - 4';
        div.append(t);

        this.divHost.append(div);
    }
}

const getData = async () => {
    const carsDataResponse = await fetch('https://storage.googleapis.com/tfjs-tutorials/carsData.json');
    const carsData = await carsDataResponse.json();
    const cleaned = carsData.map(car => ({
      mpg: car.Miles_per_Gallon,
      horsepower: car.Horsepower,
    }))
    .filter(car => (car.mpg != null && car.horsepower != null));
  
    return cleaned;
};